
ul {
  list-style: initial;
}


ul,
ol {
  padding-left: 1em;
  margin: 1em 0em;

  ul,
  ol {
    padding-left: 1em;
    margin-top: @typography[base-margin];
  }
}

.list {
  &-unstyled {
    list-style: none;
  }

  &-inline {
      padding: 0em;
      position: relative;
      display: block;

    &-item {
      display: inline-block;
    }
  }
}
